<template>
    <section class="query-page">
        <Header>查询</Header>
        <Ccontent></Ccontent>
        <Cnav   :ins="2" ></Cnav>
        <Cmask></Cmask>
    </section>
</template>

<script>

   
   import Header from '@/components/header.vue';
    import Cnav from '@/components/nav.vue';
    import Ccontent from './comps/content.vue';
    
     import Cmask from './comps/mask';
    export default {
        components: {
            Ccontent,
            Cnav,
            Cmask,
            Header
        },
        data() {
            return {
                
            };
        },
        created() {
            
        },
        mounted(){
           
        },
        methods: {
            loadMore() {
               
            },
            send(){

                this.$http
                    .post('/hackathon/infer', {
                        id:111,
                        is_default: 111,
                        tg_default: 1
                    })
                    .then(err=>{
                          console.log(err);
                    });
            },
            getArticleList() {
                let url = '/youyi/common/getArticle';
                let params = this.search;
                this.$http({
                    method: 'get',
                    url,
                    params
                }).then(res => {
                    let list = res.data.articleInfo || [];
                    if (list.length) {
                        list.forEach(item => {
                            this.list.push(item);
                        });
                        this.busy = false;
                        this.search.page++;
                    }
                    else {
                        this.isNoMoreData = true;
                    }
                });
            }
        }
    };
</script>

<style scoped>
    .query-page{
        position:relative;
    }
</style>



